@import url('https://fonts.googleapis.com/css2?family=Abel&family=Barlow:ital,wght@1,200;1,400&family=Gothic+A1:wght@300&family=Hepta+Slab:wght@200;300;400;500&family=Luckiest+Guy&family=Manrope:wght@200&family=Orbitron:wght@500&family=Prompt&family=Roboto+Slab:wght@300&family=Unbounded:wght@200&display=swap');
.footer{
    width: 100%;
    height: 100vh;
    background-color: black;
    color: white;
    padding-top: 50px;
    position: fixed;
    z-index: -2;
    bottom: 0;
    left:0;
}

.footer-info{
    font-size:35px;
    padding:30px 0 30px 50px;
    width: 70%;
    margin:0 auto;
}

.footer-info > h1{
    padding-bottom: 15px;
    font-family: 'Barlow', sans-serif;
}

.footer-contact{
   font-size: 22px; 
   padding: 100px 0 30px 50px;
   width: 70%;
   margin: 0 auto;
}
.footer-sns{
    width:70%;
    position: absolute;
    bottom: 40px;
    left:0;
    right:0;
    margin:0 auto;
    display: grid;
    grid-template-columns: 2fr 1fr;
    align-items: center;
}
.sns-links{
    display: grid;
    width: 100%;
    grid-template-columns: 1fr 1fr;
}

.linkedin, .github{
    color: white;
    font-size: 35px;
    margin: 0 auto;
    padding-bottom: 10px;
    border-bottom: 4px solid transparent;
}

.linkedin:hover, .github:hover{
    border-color: white;
    transition: all 0.3s ease-out;

}

@media screen and (max-width :960px) and (min-width:224px) {
    .footer-info{
        width: 100%;
        text-align: center;
        padding: 30px;
        height: auto;
        font-size: 20px;
    }
    .footer-contact{
        font-size: 14px;
        padding: 70px 0 3px 0;
        text-align: center;
    }
    
    .footer-sns{
        grid-template-columns: 1fr;
        text-align: center;
    }
    #desing{
        margin-bottom: 30px;
    }
}
@media screen and (max-width:224px){
    .footer-info{
        width: 100%;
        text-align: center;
        padding: 40px 10px 0 10px;
        height: auto;
        font-size: 20px;
    }
    .footer-info > i{
        margin-top: 30%;
        font-size: 11px;
    }
    .footer-contact{
        font-size: 14px;
        padding: 0 0 3px 0;
        text-align: center;
    }
    
    .footer-sns{
        grid-template-columns: 1fr;
        text-align: center;
    }
    #desing{
        margin-bottom: 30px;
    } 
}