
@import url('https://fonts.googleapis.com/css2?family=Abel&family=Barlow:ital,wght@1,200&family=Gothic+A1:wght@300&family=Hepta+Slab:wght@200;300;400;500&family=Luckiest+Guy&family=Manrope:wght@200&family=Prompt&family=Roboto+Slab:wght@300&display=swap');
 
.cover-container{
    height: 100vh;
    width: 100%;
    object-fit: contain;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.5);
    display: flex;
    flex-direction: column;
    justify-content: center ;
    align-items: center;
}
.video{
    object-fit: cover;
    height: 100vh; 
    width: 100%;
    position: absolute;
    z-index: -1;
}

.cover-container > h1{
    color: white;
    font-size:5em ;
    font-family: 'Orbitron', sans-serif;
    text-align: center;
}
.cover-container > h2{
    color: white;
    font-size:2em ;
    font-family: 'Barlow', sans-serif;
}

.cover-container > p{
    margin-top: 8px;
    color: white;
    font-size: 32px;
}

@media screen and (max-width:960px) and (min-width:369px) {
    .cover-container > h1{
        font-size: 250%;
        text-align: center;
    }
    .cover-container > h2{
        font-size: 30px;
        text-align: center;
    }
    .cover-container > p{
        text-align: center;
        font-size: 22px;
        width: 85%;
    }
    
}
@media screen and (max-width:368px) {
    .cover-container > h1{
        color: white;
        font-size :1.2em;;
        font-family: 'Orbitron', sans-serif;
        text-align: center;
    }
    .cover-container > h2{
        font-size: 1em;
        text-align: center;
    }
    .cover-container > p{
        text-align: center;
        font-size: 1.5em;
        width: 85%;
    }
}