@import url('https://fonts.googleapis.com/css2?family=Abel&family=Barlow:ital,wght@1,200;1,400&family=Gothic+A1:wght@300&family=Hepta+Slab:wght@200;300;400;500&family=Luckiest+Guy&family=Manrope:wght@200&family=Orbitron:wght@500&family=Prompt&family=Roboto+Slab:wght@300&family=Unbounded:wght@200&display=swap');
.separator2-container{
    width: 100%;
    height: 300px;
    background-color: white;
    color: black;
    justify-content: center;
    /* margin-bottom: 100vh; */
}

.separator2{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    padding: 0 20px 0 20px;
    
}
.separator2 > h1{
   font-family: 'Barlow', sans-serif; 
   font-size:30px;
   text-align: center;
}

@media screen and (max-width:960px) {
    .separator2-container{
        height: 200px;
        width: 100%;
    }

    .separator2 > h1{
        text-align: center;
        font-size: 23px;
    }
}
@media screen and (max-width:1024px) and (min-width:961px) {
    .separator2-container{
        height: 200px;
        width: 100%;
        justify-content: center;
    }

    .separator2 > h1{
        text-align: center;
        font-size: 27px;
        padding: 0 3% 0 3%;
    }
}
@media screen and (max-width:320px) {
    .separator2 >h1{
        font-size: 15px;
    }
    
}