@import url('https://fonts.googleapis.com/css2?family=Abel&family=Barlow:ital,wght@1,200;1,400&family=Gothic+A1:wght@300&family=Hepta+Slab:wght@200;300;400;500&family=Libre+Baskerville:ital@1&family=Luckiest+Guy&family=Manrope:wght@200&family=Orbitron:wght@500&family=Prompt&family=Roboto+Slab:wght@300&family=Unbounded:wght@200&display=swap');
.language-container{
    width: 100%;
    height: 250px;
    background-color: black;
    color: white;
    /* margin-bottom: 100vh; */
}

.language-desc{
    display: flex;
    justify-content: center;
    padding: 10px 0 20px 0;
    align-items: center;
}
.language-desc > h1{
    font-size: 40px;
    font-family: 'Manrope', sans-serif;
}
.language-img > img{
    width: 100%;
    height: 200px;
}

@media screen and (max-width:960px) and (min-width:408px){
    .language-container{
        height: 200px;
        width: 100%;
        padding-top: 20px;
    }
    .language-desc{
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 10px 0 10px 0;
    }
    .language-img > img{
        width: 100%;
        height: 100%;
    }
}
@media screen and (max-width:1024px) and (min-width:961px) {
    .language-container{
        height: 200px;
        width: 100%;
        justify-content: center;
    }

    .language-img > img{
        width: 100%;
        height: 100%;
    }
}
@media screen and (max-width:407px) {
    .language-desc > h1{
        font-size: 150%;
    }
    .language-img > img{
        width: 100%;
        height: 100%;
    }
    
}