@import url('https://fonts.googleapis.com/css2?family=Abel&family=Barlow:ital,wght@1,200;1,400&family=Gothic+A1:wght@300&family=Hepta+Slab:wght@200;300;400;500&family=Luckiest+Guy&family=Manrope:wght@200&family=Orbitron:wght@500&family=Prompt&family=Roboto+Slab:wght@300&family=Unbounded:wght@200&display=swap');
.about-container {
    display: flex;
    background-color: white;
    color: black;
    height: 100%;
    padding: 10px 0 10px 0;
}

.about-desc {
    /* margin-top: 170px;  */
    /* margin-bottom: 300px; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 55%;
    padding: 0 100px 0 100px;
    margin:0;
}

.about-desc > h3{
    /* margin-top: 100px; */
    font-family: 'Manrope', sans-serif;
    font-size: 32px;
}

.about-desc > p{
  line-height: 1.5;
  text-align: justify;
  font-size: 18px;
  color:black;
  font-family: 'Barlow', sans-serif;  
}

.about-img{
  width: 37%;
  height: 86vh;
  display:flex;
  overflow: hidden;
  padding-left: 30px;
}
#inicio{
  margin-top: 10px;
}
#fin{
  margin-top: 30px;
}

@media screen and (max-width:960px) and (min-width:673px){
  .about-container{
    flex-direction: column;
    padding: 0;
    height:100%;
    /* width: 100%; */
  }
  
  .about-desc{
    width: 90%;
    margin:0 auto;
    padding: 50px 20px 50px 20px;
  }
  
  .about-desc > h3{
    margin:0;
    font-size: 25px;
    text-align: center;
    margin-bottom: 10px;
  }
 
  .about-img{
    width: 100%;
    height: 100%;
    margin-bottom: 50px;
    padding: 0;
    padding: 40px 10% 50px 10%;
  } 
 
  .about-img > img{
    width: 100%;
  }
  .about-desc > p{
  line-height: 1.5;
  text-align: justify;
  font-size: 20px;
  color:black;
  font-family: 'Barlow', sans-serif;  
  }
  #inicio{
    margin: 10px 0 10px 0;
    
  }
  #fin{
    margin-top: 30px;
    /* margin-bottom: 50px; */
  }
}
@media screen and (max-width:672px) {
  .about-container{
    flex-direction: column;
    padding: 0;
    height:100%;
    /* width: 100%; */
  }
  
  .about-desc{
    width: 90%;
    margin:0 auto;
    padding: 50px 20px 5% 20px;
  }
  
  .about-desc > h3{
    margin:0;
    font-size: 25px;
    text-align: center;
    margin-bottom: 10px;
  }
 
  .about-img{
    width: 100%;
    height: 100%;
    margin-bottom: 50px;
    padding: 0;
    padding: 40px 0 0 0;
  }
 
  .about-img > img{
    width: 100%;
  }
  .about-desc > p{
  line-height: 1.5;
  text-align: justify;
  font-size: 20px;
  color:black;
  font-family: 'Barlow', sans-serif;  
  }
  #inicio{
    margin: 10px 0 10px 0;
    
  }
  #fin{
    margin-top: 30px;
    /* margin-bottom: 50px; */
  }
}
@media screen and (max-width:1024px) and (min-width:961px) {
  .about-container{
    display: flex;
    padding: 0;
    height: 90vh;
    align-items: center;
    margin: 0;
  }
  
  .about-desc{
    width: 55%;
    height: 90vh;
    margin:0 auto;
    padding: 0 10px 0 50px;
  }
 
  .about-desc > h3{
    margin-top: 5%;
    font-size: 32px;
    text-align: center;
    /* margin-bottom: 10px; */
  }
  .about-desc > p{
    line-height: 1.5;
    text-align: justify;
    font-size: 100%;
    color:black;
    font-family: 'Barlow', sans-serif;  
    }
 
  .about-img{
    width: 70%;
    padding: 0 40px 0 60px;
  }
 
  .about-img > img{
    width: 100%;
    height: 100vh; 
  }
  #inicio{
    margin-top: 10px;
  }
  
  #fin{
    margin-top: 30px;
    padding-bottom: 70px;
  }
}