@import url('https://fonts.googleapis.com/css2?family=Abel&family=Barlow:ital,wght@1,200;1,400&family=Gothic+A1:wght@300&family=Hepta+Slab:wght@200;300;400;500&family=Luckiest+Guy&family=Manrope:wght@200&family=Orbitron:wght@500&family=Prompt&family=Roboto+Slab:wght@300&family=Unbounded:wght@200&display=swap');

.separator-container{
        width: 100%;
        height: 300px;
        background-color: black;
        color: white;
        justify-content: center;
        /* margin-bottom: 100vh; */
    }
    
 .separator{
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0 2% 0 2%;
        height: 100%;
    }
.separator > h1{
       /* width: 100%;  */
       font-family: 'Barlow', sans-serif; 
       font-size:30px;
       text-align: center;
    }
    
    @media screen and (max-width:960px) {
        .separator-container{
            height: 200px;
            width: 100%;
        }
    
        .separator > h1{
            text-align: center;
            font-size: 23px;
        }
    }
    @media screen and (max-width:1024px) and (min-width:961px) {
        .separator-container{
            height: 200px;
            width: 100%;
            justify-content: center;
        }
    
        .separator > h1{
            text-align: center;
            font-size: 27px;
            padding: 0 3% 0 3%;
        }
    }
    @media screen and (max-width:320px) {
        .separator >h1{
            font-size: 18px;
        }
        
    }
