@import url('https://fonts.googleapis.com/css2?family=Abel&family=Barlow:ital,wght@1,200;1,400&family=Gothic+A1:wght@300&family=Hepta+Slab:wght@200;300;400;500&family=Libre+Baskerville:ital@1&family=Luckiest+Guy&family=Manrope:wght@200&family=Orbitron:wght@500&family=Prompt&family=Roboto+Slab:wght@300&family=Unbounded:wght@200&display=swap');
.mision-vision-container{
    display: flex;
    background-color: white;
    color: black;
    height: 90vh;
    padding: 10px 0 10px 0;
    width: 100%;
    
}
.mision-vision-desc{
    /* margin: 250px 0 300px 0; */
    display: flex;
    text-align: center;
    flex-direction: column;
    justify-content: center;
    width: 55%;
    padding: 0 100px 0 100px;
}

.mision-vision-desc > h4{
    padding: 0;
    line-height: 1.5;
    text-align: center;
    font-size: 22px;
    font-family: 'Libre Baskerville', serif;
}
.mision-vision-desc > h3{
    font-size: 32px;
    
    font-family: 'Manrope', sans-serif;

    margin:2% 0 0 0;
}
#first_title{
    /* margin-top: 0px; */
    margin:0;
}


.mision-vision-desc > p{
    line-height: 1.5;
    text-align: justify;
    font-size: 18px;
    font-family: 'Barlow', sans-serif;
    margin: 0;
    /* margin-top: 5px; */
}

.mision-vision-img{
    width: 47%;
    height: 86vh;
    display:flex;
    overflow: hidden;
    margin-left: 100px;
    }
#fin{
    margin: 0;
    padding: 0;
}

@media screen and (max-width:1024px) and (min-width:961px) {
    .mision-vision-container{
      display: flex;
      padding: 0;
      height: 90vh;
      align-items: center;
      margin: 0;
      width: 100%;
    }
  
    .mision-vision-desc{
      width: 55%;
      margin:0 auto;
      padding: 0 40px 0 40px;
      /* TODO terminar de arreglae esto */
      /* padding: 0; */
    }
    
    .mision-vision-desc > h3{
        font-size: 150%;
        text-align: center;
    }
    .mision-vision-desc > p{
        font-size: 100%;
        text-align: justify;
    }

    
    .mision-vision-img{
        width: 60%;
        height: 80vh;
        margin:0 0 0 5%;
        padding: 0;
        /* margin: 0; */
        
    }
   
    .mision-vision-img > img{
        width: 100%;
        height:80vh;        
        margin: 0;
        padding: 0;
    }

    #first_title{
        margin:0 ;
    }
    #fin{
        margin-top: 7px;
        padding: 0;
    }
}


@media screen and (max-width:960px) and (min-width:321px){
    .mision-vision-container{
      /* padding-top: 20px; */
      flex-direction: column;
      padding: 0;
      width: 100%;
      height: auto;
      align-items: center;
      /* padding: 0 0 0 0; */
      /* padding-bottom: 20px; */
    }
  
    .mision-vision-desc{
      width: 90%;
      margin:0 auto;
      padding: 0 0 10px 0;
    }

    .mision-vision-desc > p{
        font-size: 130%;
        margin: 0;
    }

    .mision-vision-desc >h4{
        font-size: 130%;
    }
    
    .titles{
        font-size: 20px;
        text-align: center;
        padding-top: 20px;
    }
    
    .mision-vision-desc > h3{
        font-size: 34px;
        margin-top: 10px;
    }
    #first_title{
        margin: 0;
    }
    #fin{
        margin: 0 0 30px 0;
    }
    .mision-vision-img{
        width: 100%;
        padding: 0;
        margin:50px 0 0 0;
    }
   
    .mision-vision-img > img{
        width: 100%;
        height: 500px;
    }
  
}
@media screen and (max-width:320px) {
    .mision-vision-container{
        flex-direction: column;
        padding: 0;
        height: 100%;
        align-items: center;
        margin: 0;
      }
      .mision-vision-desc{
        width: 100%;
        margin:0 auto;
        padding: 0 10px 0 10px;
      }
      
      .mision-vision-desc > h3{
          font-size: 24px;
          text-align: center;
      }
      .mision-vision-desc > p{
          font-size: 18px;
          text-align: justify;
      }
  
      
      .mision-vision-img{
          width: 100%;
          height:100%;
          padding:0;
          margin: 10px 0 0  0;           
      }
     
      .mision-vision-img > img{
          width: 100%;
      }
  
      #first_title{
          margin-top: 30px;
      }
      #fin{
          /* margin-top: 7px; */
          padding: 7px 0 12px 0;
      }
}
